.projects {
    background-color: #fff;

    .projects-section {
        padding-top: 4rem;
    }

    figure {
        position: relative;
        overflow: hidden;
        border-radius: 12px;

        &:hover {

            figcaption,
            .overlay {
                opacity: 1;
            }

            img {
                transform: scale(1.1);
            }
        }

    }

    figcaption,
    .overlay {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        -webkit-transition: .45s ease-in-out;
        transition: .45s ease-in-out;

        h1,
        h2 {
            color: #fff;
            margin-bottom: 1.8rem;
            font-weight: 500;


        }

        .title {
            font-size: 1.2rem;
        }

        .button {

            .icon {
                svg {
                    width: 8px;
                    top: 3px;
                    position: relative;
                }

            }
        }
    }

    figcaption {
        padding: 2.5rem 2rem;
        z-index: 1;
    }


    .thumbnails {
        -webkit-transform: scale(1);
        transform: scale(1);
        -webkit-transition: .25s ease-in-out;
        transition: .25s ease-in-out
    }

    .overlay {
        background-color: #293347;
    }


}